import { createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';
import Notiflix from 'notiflix';

axios.defaults.baseURL = 'https://connections-api.herokuapp.com';

const token = {
  set(token) {axios.defaults.headers.common.Authorization = `Bearer ${token}`;},
  unset() {axios.defaults.headers.common.Authorization = '';}
}

export const register = createAsyncThunk(
  'auth/register',
  async credentials => {
    try {
      const { data } = await axios.post('/users/signup', credentials);
      token.set(data.token);
      Notiflix.Notify.success('You are logged in');
      return data;
    } catch (error) {
      Notiflix.Notify.failure(`Oops, Something is wrong! ${error.message}`);
      return console.log(error.message);
    }
  }
);

export const logIn = createAsyncThunk(
  'auth/login',
  async credentials => {
    try {
      const { data } = await axios.post('/users/login', credentials);
      token.set(data.token);
      Notiflix.Notify.success('You are logged in');
      return data;
    } catch (error) {
      Notiflix.Notify.failure(`Oops, Something is wrong! ${error.message}`);
      return console.log(error.message);
    }
  }
);

export const logOut = createAsyncThunk('auth/logout', async () => {
  try {
    await axios.post('/users/logout');
    token.unset();
    Notiflix.Notify.success("You are logged out")
  } catch (error) {
    Notiflix.Notify.failure(`Oops, Something is wrong! ${error.message}`);
    return console.log(error.message);
  }
});

export const refreshUser = createAsyncThunk(
  'auth/refresh',
  async (_, thunkAPI) => {
    const state = thunkAPI.getState();
    const persistedToken = state.auth.token;

    if (persistedToken === null) {
      return thunkAPI.rejectWithValue('Unable to fetch user');
    }
    token.set(persistedToken);
    try {
      const { data } = await axios.get('/users/current');
      return data;
    } catch (error) {
      Notiflix.Notify.failure(`Oops, Something is wrong! ${error.message}`);
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);